export const setTheme = () => {
    return {
        type: 'TOGGLE'
    };
}

export const changeLanguage = (data) => {
    return{
        type: 'CHANGE',
        payload: data
    };
}

export const toggleBurger = (data) => {
    return{
        type: 'TOGGLE_BURGER',
        payload: data
    }
}

export const setSearchInput = (data) => {
    return {
        type: 'SET_SEARCH_INPUT',
        payload: data
    }
}

export const setSearchFilter = (data) => {
    return {
        type: 'SET_SEARCH_FILTER',
        payload: data
    }
}

export const setOpenFilter = (data) => {
    return {
        type: 'SET_OPEN_FILTER',
        payload: data
    }
}

export const selectAuthor = (data) => {
    return {
        type: 'SELECT_AUTHOR',
        payload: data
    }
}

export const selectYear = (data) => {
    return {
        type: 'SELECT_YEAR',
        payload: data
    }
}
