import React, {useState} from 'react';
import '../Styles/CSS/Header.css';
import ReactTooltip from 'react-tooltip';
import Hamburger from "./Hamburger";
import LanguageSwitch from './LanguageSwitch';
import {useSelector,useDispatch} from 'react-redux';
import {setTheme, toggleBurger} from '../Actions';
import {Link, useRouteMatch} from 'react-router-dom';

export default function Header () {

    console.log('Header');

    const darkMode = useSelector(state=> state.darkMode);
    const burgerState = useSelector(state=> state.burgerState);
    const dispatch = useDispatch();
    
    function exitMenuHandler () {
        burgerState===true && dispatch(toggleBurger(false));
    }

    function CustomLink ({to,exact,children,classes='',onClick}) {
        const match = useRouteMatch({
            exact: exact,
            path: to
        });

        return(
            <Link className={match ? 'active ' + classes : ''} to={to} onClick={onClick}>{children}</Link>
        )
    }

    return(
        <div className={`Header ${burgerState===true ? 'open' : ''}`}>

                <Link to="/MagicalBooks" className="logo" onClick={exitMenuHandler}>
                    <img src={require('../Assets/img/magical-logo.svg').default} alt=""/>
                </Link>
            <div className="the-rest">
                    <nav>
                        <CustomLink to="/readers" exact={true} onClick={exitMenuHandler}>For readers</CustomLink>
                        <CustomLink to="/authors" exact={true} onClick={exitMenuHandler}>For authors</CustomLink>
                        <CustomLink to="/about" exact={true} onClick={exitMenuHandler}>About</CustomLink>
                        <CustomLink to="/contact" exact={true} onClick={exitMenuHandler}>Contact</CustomLink>
                    </nav>
                <div className="actions">
                    <div className="theme-switch" onClick={()=>{dispatch(setTheme())}}>
                        {darkMode ? (
                            <img data-tip="Switch to light mode" src={require('../Assets/img/sun.svg').default} alt=""/>
                        ) : (
                            <img data-tip="Switch to dark mode" src={require('../Assets/img/moon.svg').default} alt=""/>
                        )}
                    </div>
                    <LanguageSwitch />
                    <div className="sign-in-btn">
                        <img src={require('../Assets/img/user.svg').default} alt=""/>
                        <span>Personal Account</span>
                    </div>
                </div>
            </div>
            <Hamburger />
        </div>
    )
}