import React, { useEffect } from 'react';
import '../Styles/CSS/Results.css';
import ReactTooltip from 'react-tooltip';
import {useParams} from 'react-router-dom';

import bookList from '../bookList.json';
import ResultsFilter from './ResultsFilter';
import BookCard from './BookCard';

import {useSelector,useDispatch} from 'react-redux';
import {selectAuthor,selectYear, setSearchFilter, setSearchInput} from '../Actions';

export default function Results () {

    console.log('Results');

    useEffect(()=>{
        ReactTooltip.rebuild();
    });
    
    const selectedFilterGroup = useSelector(state=> state.selectedFilterGroup);
    const theAuthor = selectedFilterGroup.author;
    const theYear = selectedFilterGroup.year;
    const dispatch = useDispatch();

    const searchQuery = useSelector(state=>state.searchQuery);
    const params = useParams();
    
    useEffect(()=>{
        if((params.input && params.input!==searchQuery.input) ||
            (params.filter && params.filter!==searchQuery.filter)) {
            dispatch(setSearchInput(params.input || ''));
            dispatch(setSearchFilter(params.filter || ''));
        }
    },[]);

    useEffect(()=>{
        return ()=>{
            dispatch(setSearchInput(''));
        }
    },[]);

    const searchInput = params.input || searchQuery.input;
    const searchFilter = params.filter || searchQuery.filter;
    const resultsList = bookList.filter(book=>{
        if(searchFilter==='all') {
            return book.title.toLowerCase().includes(searchInput.toLowerCase())
            || book.author.toLowerCase().includes(searchInput.toLowerCase())
        }
        else if(searchFilter==='title') {
            return book.title.toLowerCase().includes(searchInput.toLowerCase())
        }
        else {
            return book.author.toLowerCase().includes(searchInput.toLowerCase())
        }
    });

    const filteredResults = resultsList.filter(item => {
        if(theAuthor!=='All'&&theYear!=='All'){
            return item.author===theAuthor && item.date.year===theYear;
        }
        else if(theAuthor!=='All'){
            return item.author===theAuthor;
        }
        else if(theYear!=='All'){
            return item.date.year===theYear;
        }
        else {
            return item;
        }
    });

    const resultsAuthors = Array.from(new Set(resultsList.map(book => book.author))).sort();
    const resultsYears = Array.from(new Set(resultsList.map(book => book.date.year))).sort(function(a,b){return b-a});

    function clearFilters() {
        dispatch(selectAuthor('All'));
        dispatch(selectYear('All'));
    }

    return (
        <div className="Results">
            <div className="filters">
                <ResultsFilter uKey={1} choicesList={resultsAuthors} toShow={theAuthor} defaultChoice="Author" action={selectAuthor} />
                <ResultsFilter uKey={2} choicesList={resultsYears} toShow={theYear} defaultChoice="Year" action={selectYear} />
                { theAuthor!=='All' || theYear!=='All' ?
                <div
                    className="clear-filters"
                    onClick={clearFilters}
                    data-tip="Clear filters"
                ><img src={require('../Assets/img/cancel.svg').default} alt="" /></div> :
                ''
                }
            </div>
            <div className="results-list">
                {filteredResults.map(result => (<BookCard key={result.index} bookData={result} />))}
            </div>
        </div>
    )
    
}