import React, { Fragment } from 'react';
import '../Styles/CSS/BestSellers.css';

function SingleBook ({aBook}) {
    return (
        <div className="SingleBook">
            <div className="cover"><img src={require(`../Assets/book-covers/${aBook.cover}`).default} alt="a book"/></div>
            <div className="data">
                <p className="title">{aBook.title}</p>
                <p className="author">{aBook.author}</p>
                <p className="year">{aBook.date.year}</p>
            </div>
        </div>
    )
}

export default function BestSellers({bookData}) {
    return(
        <div className="BestSellers">
            <div className="header">
                <p className="heading">This Month's Best Sellers</p>
                <div className="btn">
                    <img src={require('../Assets/img/right-arrow.svg').default} alt=""/>
                </div>
            </div>
            {bookData.map((aBook,index) => <SingleBook key={index} aBook={aBook} />)}
        </div>
    );
}