import React, {useState} from 'react';
import '../Styles/CSS/LanguageSwitch.css';
import {useSelector, useDispatch} from 'react-redux';
import {changeLanguage} from '../Actions';

export default function LanguageSwitch () {

    const dispatch = useDispatch();
    const language = useSelector(state=>state.language);

    const [langBoxState,setlangBoxState] = useState(false);

    function langBoxStateHandler () {
        setlangBoxState(!langBoxState);
    }

    return(
        <div className="lang-switch" onClick={langBoxStateHandler}>
            <p>{language}</p>
            <div className={`${langBoxState ? 'languages open' : 'languages closed'}`}>
                <p onClick={()=>{langBoxStateHandler();dispatch(changeLanguage('EN'))}}>English</p>
                <p onClick={()=>{langBoxStateHandler();dispatch(changeLanguage('RU'))}}>Russian</p>
            </div>
        </div>
    )
}