import React, { useState, useRef, useEffect } from 'react';
import '../Styles/CSS/Search.css';
import {setSearchInput, setSearchFilter} from '../Actions';
import {useSelector, useDispatch} from 'react-redux';
import {BrowserRouter as Router, Link} from 'react-router-dom';

export default function Search () {

    console.log('Search');

    const searchButton = useRef();
    const prevInputState = useRef('none');

    const searchQuery = useSelector(state=> state.searchQuery);
    const dispatch = useDispatch();
    const [inputFocus,setInputFocus] = useState(false);
    const [mobileFilters,setMobileFilters] = useState(false);

    const skipInitial = useRef(false);
    useEffect(()=>{
        if(skipInitial.current){
            if(!(!prevInputState.current && !searchQuery.input)){
                searchButton.current.click();
            }
            prevInputState.current = searchQuery.input;
        }
        else{
            skipInitial.current = true;
        }
    },[searchQuery.input,searchQuery.filter]);

    function mobileFilterHandler() {
        setMobileFilters(!mobileFilters);
    }

    function focusHandler(e) {
        if(e.type==='focus'){
            setInputFocus(true);
            return
        }
        setInputFocus(false);
    }

    function changeHandler(e) {
        dispatch(setSearchInput(e.target.value));
    }

    return (
        <div className="Search">
            <div className={`search-field ${inputFocus ? 'focused' : ''}`}>
                <input
                    onFocus={focusHandler}
                    onBlur={focusHandler}
                    onInput={changeHandler} type="text" placeholder="Search off our extensive library"
                    value={searchQuery.input}
                />
                <div onClick={mobileFilterHandler} className="filters-btn"><img src={require('../Assets/img/filter.svg').default} alt=""/></div>
                <div className={`search-filters ${mobileFilters ? 'active' : ''}`}>
                    <p onClick={()=>{dispatch(setSearchFilter('all')); mobileFilterHandler()}} className={`filter ${searchQuery.filter==='all' && 'active'}`}>All fields</p>
                    <p onClick={()=>{dispatch(setSearchFilter('authors')); mobileFilterHandler()}} className={`filter ${searchQuery.filter==='authors' && 'active'}`}>Authors</p>
                    <p onClick={()=>{dispatch(setSearchFilter('title')); mobileFilterHandler()}} className={`filter ${searchQuery.filter==='title' && 'active'}`}>Title</p>
                </div>
            </div>
            <Link to={searchQuery.input!=="" ? `/search/${searchQuery.input}/${searchQuery.filter}` : '/MagicalBooks'}>
                <div ref={searchButton} className="search-btn"></div>
            </Link>
        </div>
    )
}