import React from 'react';
import '../Styles/CSS/Home.css';
import bookList from '../bookList.json';
import BookCard from './BookCard';
import BestSellers from './BestSellers';

export default function Home () {
    
    function randomItems (arr,num) {
        const total= arr.length;
        if(num>total){
            console.log('Requested number of random items is not available in the provided array');
            return;
        }
        const selected = [];
        while(selected.length<num){
            const random = Math.floor(Math.random()*total);
            if(selected.indexOf(arr[random])<0){
                selected.push(arr[random]);
            }
        }
        return selected;
    }

    const recents = randomItems(bookList,11);
    const bestSellers = randomItems(bookList,3);

    return (
        <div className="Home">
            <div className="heading-large-screen">Recent additions</div>
            <div className="the-list">
                <BestSellers bookData={bestSellers} />
                <div className="heading-small-screen">Recent additions</div>
                {
                    recents.map(book=> <BookCard key={book.index} bookData={book} />)
                }
            </div>
        </div>
    );

}