import {combineReducers} from 'redux';

const darkMode = (state=false,action) => {
    switch (action.type) {
        case 'TOGGLE':
            return !state;
        default:
            return state;
    }
}

const language = (state='EN',action) => {
    switch (action.type) {
        case 'CHANGE':
            return action.payload;
        default:
            return state;
    }
}

const burgerState = (state='untouched',action) => {
    switch (action.type) {
        case 'TOGGLE_BURGER':
            return action.payload;
        default:
            return state;
    }
}

const searchQuery = (state={input: '',filter: 'all'},action) => {
    switch (action.type) {
        case 'SET_SEARCH_INPUT':
            return {...state, input: action.payload};
        case 'SET_SEARCH_FILTER':
            return {...state, filter: action.payload};
        default:
            return state;
    }
}

const whichIsOpen = (state='none',action) => {
    switch (action.type) {
        case 'SET_OPEN_FILTER':
            return action.payload;
        default:
            return state;
    }
}

const selectedFilterGroup = (state={author: 'All', year: 'All'},action) => {
    switch (action.type) {
        case 'SELECT_AUTHOR':
            return {...state,author: action.payload}
        case 'SELECT_YEAR':
            return {...state,year: action.payload}
        default:
            return state;
    }
}


const allReducers = combineReducers({
    darkMode: darkMode,
    language: language,
    burgerState: burgerState,
    searchQuery: searchQuery,
    whichIsOpen: whichIsOpen,
    selectedFilterGroup: selectedFilterGroup
});

export default allReducers;

