import React from 'react';
import '../Styles/CSS/BookCard.css';

export default function BookCard ({bookData}) {
    return (
        <div className="BookCard">
            <div className="cover">
                <div className="price">{bookData.price}</div>
                <img src={require(`../Assets/book-covers/${bookData.cover}`).default} alt=""/>
            </div>
            <p className="title">{bookData.title}</p>
            <p className="author">{bookData.author}</p>
            <p className="year">{bookData.date.year}</p>
        </div>
    );
}