import React, { useEffect} from 'react';
import '../Styles/CSS/ResultsFilter.css';
import {useSelector,useDispatch} from 'react-redux';
import {setOpenFilter} from '../Actions';

export default function ResultsFilter ({uKey,toShow,defaultChoice,choicesList,action}) {

    console.log('Results filter')

    const whichIsOpen = useSelector(state=> state.whichIsOpen);
    const dispatch = useDispatch();

    useEffect(()=>{
        function checkOutsideClick(e) {
            dispatch(setOpenFilter('none'));
        }
        document.addEventListener('click',checkOutsideClick);
        return () => {
            document.removeEventListener('click',checkOutsideClick);
        }
    })

    function clickHandler (e) {
        e.stopPropagation();
        dispatch(setOpenFilter(uKey));
    }

    function updateSelectedFilters(e) {
        e.stopPropagation();
        dispatch(action(e.target.innerText));
        dispatch(setOpenFilter('none'));
    }

    return (
        <div className="ResultsFilter" onClick={clickHandler}>
            <p className="current">{toShow==='All' ? defaultChoice : (toShow.length>15 ? `${toShow.substring(0,14)}...` : toShow)}</p>
            <img src={require('../Assets/img/down-arrow.svg').default} alt=""/>
            <div className={`choices-list ${whichIsOpen===uKey ? 'open' : ''}`}>
                <div className="list-container">
                    <p onClick={updateSelectedFilters}>All</p>
                    {choicesList.map((item,index)=>{
                        return <p key={index} onClick={updateSelectedFilters}>{item}</p>
                    })}
                </div>
            </div>
        </div>
    );
}