import React, {useState} from 'react';
import '../Styles/CSS/Hamburger.css';
import {useSelector,useDispatch} from 'react-redux';
import {toggleBurger} from '../Actions';

export default function Hamburger () {

    const burgerState = useSelector(state => state.burgerState);
    const dispatch = useDispatch();

    function burgerHandler () {
        if(burgerState!=='untouched') {
            if(burgerState===true) {
                return false
            }
            return true;
        }
        return true;
    }

    return (
        <div className="Hamburger" onClick={()=>dispatch(toggleBurger(burgerHandler()))}>
            <div className={`center ${burgerState!=='untouched' && (burgerState ? 'open' : 'closed')}`}></div>
        </div>
    );
}